import React, {useState} from "react";
import "./CSS/Votes.css";
import {ReactComponent as Up} from "./thumbs-up.svg";
import {ReactComponent as Down} from "./thumbs-down.svg";

function Votes(props) {

    const [textColor, setTextColor] = useState(false)
    const handleClickUp = (e) => {
        "post to DB user_id"
    };
    const handleClickDown = (e) => {
        "post to DB user_id"
    };

    return (
        <div className={textColor === true ? "votes-wrapper light-color-text" : "votes-wrapper dark-color-text"}>
            <div className="votes-thumb">
            <span className={textColor === true ? "votes-number light-color-text" : "votes-number dark-color-text"}>{props.upvotes}11</span>&nbsp;<Up onClick={handleClickUp} />
            </div>
            <div className="votes-thumb">
            <span className={textColor === true ? "votes-number light-color-text" : "votes-number dark-color-text"}>{props.downvotes}22</span>&nbsp;<Down onClick={handleClickDown} />
            </div>
        </div>
    )
};

export default Votes;